import React, { Component } from 'react';
import Dropdown from '../DropdownComponent/dropdown';
import Input from '../InputElement';
import '../InputGroup/InputGroup.scss';

class InputGroup extends Component {
  render() {
    return (
      <div className="flex">
        <Dropdown
          className="datawidth"
          dropdownDisable={this.props.dropdownDisable}
        />
        <span className="span-line" />
        <Input
          className={this.props.disabled ? 'opacity borderprop' : 'borderprop'}
          placeholder="Capacity"
          value={this.props.inputpremium1}
          disabled={this.props.disabled}
        />
        <span className="span-line" />
        <Input
          className={this.props.disabled ? 'opacity borderprop' : 'borderprop'}
          placeholder="Toilets"
          value={this.props.inputpremium2}
          disabled={this.props.disabled}
        />
      </div>
    );
  }
}

export default InputGroup;
