import React, { Component } from 'react';
import InputElement from '../../Atoms/InputElement/index.js';
import { connect } from 'react-redux';
import { Dropdown } from '../../Atoms/DropdownComponent';
import Button from '../../Atoms/Button';
import { DelayCodesActions } from '../../../Store/DelayCodes/action.js';
import { commonAction } from '../../../Store/commonActivity/action.js';

class DelayCodes extends Component {
  obj = {
    id: this.props.delayValue.id,
    category: this.props.delayValue.category,
    delaycategorygroup: this.props.delayValue.delaycategorygroup,
    description: this.props.delayValue.description,
    subcategory: this.props.delayValue.subcategory,
    controllableind: this.props.delayValue.controllableind,
  };
  componentDidMount() {}
  AddDelaysubmit = () => {
    // alert(this.props.isCreateOrupdate.isCreate)
    const keys = Object.keys(this.obj);
    if (!this.props.isCreateOrupdate.isCreate) {
      for (var i = 0; i < keys.length; i++) {
        if (keys[i] === 'id') {
          keys.splice(i, 1);
        }
      }
    }
    let flag = true;

    // keys.map(key => {
    //   if (
    //     this.obj.category === '' ||
    //     this.obj.delaycategorygroup === '' ||
    //     this.obj.controllableind === '' ||
    //     this.obj.description === ''
    //   ) {
    //     flag = false;
    //     commonAction.openAlert({
    //       title: 'Warning',
    //       content: 'Fields are empty',
    //     });
    //   }
    // });

    let newKey = [
      {
        key: 'delaycategorygroup',
        value: 'Delay Group',
      },
      {
        key: 'controllableind',
        value: 'Controllable',
      },
      {
        key: 'category',
        value: 'Delay Code',
      },
      {
        key: 'description',
        value: 'Delay Description',
      },
    ];

    for (let index = 0; index < newKey.length; index++) {
      const element = newKey[index];
      if (!this.obj[element.key]) {
        flag = false;
        commonAction.openAlert({
          title: 'Warning',
          content: element.value + ' is empty',
        });
        return false;
      }
    }

    if (flag) {
      var newObj = {};
      keys.map(key => {
        if (this.obj[key] !== '') {
          newObj[key] = this.obj[key];
        }
      });

      let isEdit = this.props.isCreateOrupdate.isCreate;
      console.log(newObj, 'testOBJ');
      if (isEdit) {
        if (newObj.subcategory.includes(newObj.category)) {
          newObj.subcategory = newObj.subcategory.replace(newObj.category, '');
        }
      }
      this.props.AddCodesubmit(newObj, isEdit);
      this.props.cancel();
    }
  };

  showSubDelayDesc = value => {
    var char = '';
    var wholechar = '';
    var original = '';
    var index = 0;
    for (var i = 0; i < value.length; i++) {
      if (value[i] === '-') {
        index = i;
        break;
      } else {
        char += value[i];
      }
    }
    for (var i = 0; i < char.length; i++) {
      if (isNaN(char[i])) {
        original += char[i];
      }
    }
    for (var i = index + 1; i < value.length; i++) {
      wholechar += value[i];
    }
    return { first: original, second: wholechar };
  };
  showWarning = () => {
    commonAction.openAlert({
      title: 'Warning',
      content: 'This is a read only field',
    });
  };

  render() {
    this.keys = Object.keys(this.props.delayValue);
    const { first, second } = this.showSubDelayDesc(
      !this.props.delayValue.subcategory
        ? ''
        : this.props.delayValue.subcategory
    );
    let controlvalue = [
      { title: 'Yes', selected: false },
      { title: 'No', selected: false },
      { title: 'EXCL', selected: false },
    ];

    controlvalue.forEach(dropdowndata => {
      if (
        this.props.delayValue.controllableind === 'Y' &&
        dropdowndata.title === 'Yes'
      ) {
        dropdowndata.selected = true;
      } else if (
        this.props.delayValue.controllableind === 'N' &&
        dropdowndata.title === 'No'
      ) {
        dropdowndata.selected = true;
      } else if (
        this.props.delayValue.controllableind === 'EXCL' &&
        dropdowndata.title === 'EXCL'
      ) {
        dropdowndata.selected = true;
      }
    });

    return (
      <div>
        <div className="ac-border border-btmnone">
          <div className="station-main-div">
            <div className="pad-20b">
              <div className="station-datalist width-50">
                <div className="station-text">
                  Delay Group<span className="mandatory-star"> *</span>
                </div>

                <div
                  onClick={
                    this.props.delayValue.delaycategorygroup === null
                      ? true
                      : this.props.delayValue.delaycategorygroup.length > 0
                      ? this.showWarning
                      : ''
                  }
                >
                  <InputElement
                    placeholder="Enter"
                    onChange={e => {
                      this.obj.delaycategorygroup = e.target.value;
                    }}
                    defaultValue={this.props.delayValue.delaycategorygroup}
                    disabled={
                      this.props.delayValue.delaycategorygroup === null
                        ? true
                        : this.props.delayValue.delaycategorygroup.length > 0
                        ? true
                        : false
                    }
                  />
                </div>
              </div>

              <div className="station-datalist width-50 pad-12l">
                <div className="station-text">
                  Controllable<span className="mandatory-star"> *</span>
                </div>
                <Dropdown
                  // placeholder="Select"
                  // title="Select"
                  list={controlvalue}
                  toggleItem={item => {
                    this.obj.controllableind = item.title;
                  }}
                  defaultValue={this.props.delayValue.controllableind}
                />
              </div>
            </div>

            <div className="pad-20b">
              <div className="station-datalist width-50">
                <div className="station-text">
                  Delay Code<span className="mandatory-star"> *</span>
                </div>
                <div
                  onClick={
                    this.props.delayValue.category === null
                      ? true
                      : this.props.delayValue.category.length > 0
                      ? this.showWarning
                      : ''
                  }
                >
                  <InputElement
                    placeholder="Enter"
                    onChange={e => {
                      this.obj.category = e.target.value;
                    }}
                    defaultValue={this.props.delayValue.category}
                    disabled={
                      this.props.delayValue.category === null
                        ? true
                        : this.props.delayValue.category.length > 0
                        ? true
                        : false
                    }
                  />
                </div>
              </div>

              <div className="station-datalist pad-12l width-50">
                <div className="station-text">
                  Delay Description<span className="mandatory-star"> *</span>
                </div>
                <div
                  onClick={
                    this.props.delayValue.subcategory === undefined
                      ? this.showWarning
                      : false
                  }
                >
                  <InputElement
                    placeholder="Enter"
                    onChange={e => {
                      this.obj.description = e.target.value;
                    }}
                    defaultValue={this.props.delayValue.description}
                    disabled={
                      this.props.delayValue.subcategory ||
                      this.props.delayValue.subcategory === ''
                        ? false
                        : true
                    }
                  />
                </div>
              </div>
            </div>

            <div className="pad-26b">
              <div className="station-datalist width-50">
                <div className="station-text">Sub Delay Code</div>
                <div
                  onClick={
                    this.props.delayValue.category === null
                      ? true
                      : this.props.delayValue.category.length > 0
                      ? this.showWarning
                      : ''
                  }
                >
                  <InputElement
                    placeholder="Enter"
                    onChange={e => {
                      this.first = e.target.value;
                      this.obj.subcategory =
                        e.target.value +
                        '-' +
                        (this.last === undefined
                          ? second
                          : this.last + e.target.value);
                    }}
                    defaultValue={
                      this.props.delayValue.subcategory === undefined
                        ? ''
                        : first
                    }
                    disabled={
                      this.props.delayValue.category === null
                        ? true
                        : this.props.delayValue.category.length > 0
                        ? true
                        : false
                    }
                  />
                </div>
              </div>

              <div className="station-datalist pad-12l width-50">
                <div className="station-text">Sub Delay Description</div>
                <div
                  onClick={
                    this.props.delayValue.category === null
                      ? true
                      : this.props.delayValue.category.length > 0
                      ? this.showWarning
                      : ''
                  }
                >
                  <InputElement
                    placeholder="Enter"
                    onChange={e => {
                      this.last = e.target.value;

                      this.obj.subcategory =
                        (this.first === undefined ? first : this.first) +
                        '-' +
                        e.target.value;
                    }}
                    defaultValue={
                      this.props.delayValue.subcategory === undefined
                        ? ''
                        : second
                    }
                    disabled={
                      this.props.delayValue.category === null
                        ? true
                        : this.props.delayValue.category.length > 0
                        ? true
                        : false
                    }
                  />
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="Rectangle-116 pad-18r">
          <Button className="cancel-btn" ButtonHandler={this.props.cancel}>
            Cancel
          </Button>
          <Button className="save-btn" ButtonHandler={this.AddDelaysubmit}>
            Save
          </Button>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = {
  AddCodesubmit: DelayCodesActions.postDelayCodesDetails,
};
const mapStateToProps = state => {
  return {};
};
export default connect(mapStateToProps, mapDispatchToProps)(DelayCodes);
