import React, { Component } from 'react';
import '../../../Utils/Aircraft.scss';
import { Dropdown } from '../../Atoms/DropdownComponent';
import Input from '../../Atoms/InputElement';
import { connect } from 'react-redux';
import { AircraftTypeActions } from '../../../Store/AircraftType/action';
import Button from '../../Atoms/Button';
import InputGroup from '../../Atoms/InputGroup';
import { commonAction } from '../../../Store/commonActivity/action';

class AircraftType extends Component {
  obj = {
    id: this.props.acTypeValue.id,
    internalaircrafttype: this.props.acTypeValue.internalaircrafttype,
    iataaircrafttype: this.props.acTypeValue.iataaircrafttype,
    fleetcategory: this.props.acTypeValue.fleetcategory,
    owner: this.props.acTypeValue.owner,
  };

  AcRegData = () => {
    const keys = Object.keys(this.obj);
    if (!this.props.isCreateOrupdate.isCreate) {
      for (var i = 0; i < keys.length; i++) {
        if (keys[i] === 'id') {
          keys.splice(i, 1);
        }
      }
    }
    let flag = true;

    let newKey = [
      {
        key: 'internalaircrafttype',
        value: 'Internal AC Type',
      },
      {
        key: 'iataaircrafttype',
        value: 'IATA AC Type',
      },
      {
        key: 'fleetcategory',
        value: 'AC Fleet Category',
      },
    ];

    for (let index = 0; index < newKey.length; index++) {
      const element = newKey[index];
      if (!this.obj[element.key]) {
        flag = false;
        commonAction.openAlert({
          title: 'Warning',
          content: element.value + ' is empty',
        });
        return false;
      }
    }

    // if (
    //   this.obj.internalaircrafttype === '' ||
    //   this.obj.iataaircrafttype === '' ||
    //   this.obj.fleetcategory === ''
    // ) {
    //   flag = false;
    //   commonAction.openAlert({
    //     title: 'Warning',
    //     content: 'Fields are empty',
    //   });
    // }
    if (flag) {
      var newObj = {};
      keys.map(key => {
        if (this.obj[key] !== '') {
          newObj[key] = this.obj[key];
        }
      });

      let isEdit = this.props.isCreateOrupdate.isCreate;

      this.props.AddAcTypesubmit(newObj, isEdit);
      this.props.cancel();
    }
  };
  showWarning = () => {
    if (this.props.isCreateOrupdate.isCreate) {
      commonAction.openAlert({
        title: 'Warning',
        content: 'IATA AC Type is read only Field cannot be edited',
      });
    }
  };

  render() {
    return (
      <div>
        <div className="ac-border border-btmnone">
          <div className="title Aircraft-Details">Aircraft Details</div>
          <div className="ac-details">
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title">
                Internal AC Type<span className="mandatory-star"> *</span>
              </div>
              <div>
                <Input
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.internalaircrafttype = e.target.value;
                  }}
                  defaultValue={this.props.acTypeValue.internalaircrafttype}
                />
              </div>
            </div>
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title">
                IATA AC Type<span className="mandatory-star"> *</span>
              </div>
              <div className="none" onClick={this.showWarning}>
                <Input
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.iataaircrafttype = e.target.value;
                  }}
                  defaultValue={this.props.acTypeValue.iataaircrafttype}
                  disabled={
                    this.props.acTypeValue.iataaircrafttype === null
                      ? true
                      : this.props.acTypeValue.iataaircrafttype.length > 0
                      ? true
                      : false
                  }
                />
              </div>
            </div>
            <div className="ac-reg-div owner-div pad-12r">
              <div className="ac-reg-title">
                AC Fleet Category<span className="mandatory-star"> *</span>
              </div>
              <div>
                <Input
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.fleetcategory = e.target.value;
                  }}
                  defaultValue={this.props.acTypeValue.fleetcategory}
                />
              </div>
            </div>
            <div className="ac-reg-div">
              <div className="ac-reg-title">Owner</div>
              <div>
                <Input
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.owner = e.target.value;
                  }}
                  defaultValue={this.props.acTypeValue.owner}
                />
              </div>
            </div>
          </div>

          <div className="title Aircraft-Details">Capacity Details</div>
          <div className="ac-details">
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title">Cargo Payload</div>
              <div>
                <Input disabled className="opacity" />
              </div>
            </div>
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title">Overall Payload</div>
              <div>
                <Dropdown dropdownDisable />
              </div>
            </div>
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title">MGT</div>
              <div>
                <Input disabled className="opacity" />
              </div>
            </div>
          </div>
          <div className="ac-details pad-7t">
            <div className="ac-reg-div Rectangle-202 pad-12r">
              <div className="ac-reg-title">Description</div>
              <div>
                <Input disabled className="opacity" />
              </div>
            </div>
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title">Crew Bank</div>
              <div>
                <Dropdown dropdownDisable />
              </div>
            </div>
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title">CMIV</div>
              <div>
                <Dropdown dropdownDisable />
              </div>
            </div>
          </div>

          <div className="ac-details pad-7t">
            <div className="ac-reg-div Rectangle-109 pad-12r">
              <div className="ac-reg-title">First Class </div>
              <div>
                <InputGroup disabled dropdownDisable className="opacity" />
              </div>
            </div>
            <div className="ac-reg-div Rectangle-109 pad-12r">
              <div className="ac-reg-title">Business Class </div>
              <div>
                <InputGroup disabled dropdownDisable className="opacity" />
              </div>
            </div>
          </div>

          <div className="ac-details pad-7t">
            <div className="ac-reg-div Rectangle-109 pad-12r">
              <div className="ac-reg-title">Premium Class </div>
              <div>
                <InputGroup disabled dropdownDisable className="opacity" />
              </div>
            </div>
            <div className="ac-reg-div Rectangle-109 pad-12r">
              <div className="ac-reg-title">Economy Class </div>
              <div>
                <InputGroup disabled dropdownDisable className="opacity" />
              </div>
            </div>
          </div>

          <div className="title Aircraft-Details">Remarks</div>
          <div className="ac-details">
            <div className="ac-reg-div reg-div">
              <div className="ac-reg-title">Remarks</div>
              <div>
                <Input disabled className="opacity" />
              </div>
            </div>
          </div>
        </div>
        <div className="Rectangle-116 pad-18r">
          <Button className="cancel-btn" ButtonHandler={this.props.cancel}>
            Cancel
          </Button>
          <Button className="save-btn" ButtonHandler={this.AcRegData}>
            Save
          </Button>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = {
  AddAcTypesubmit: AircraftTypeActions.postAircraftTypeDetails,
};
const mapStateToProps = state => {
  return {};
};
export default connect(mapStateToProps, mapDispatchToProps)(AircraftType);
