import React, { Component } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import '../../Molecules/DataSelect/DataSelect.scss';
import Stations from '../../Molecules/Stations';
import AircraftRegLanding from '../../Molecules/AircraftRegLanding';
import AircraftTypeLanding from '../../Molecules/AircraftTypeLanding';
import DelayCodesLanding from '../../Molecules/DelayCodesLanding';

class DataSelect extends Component {
  render() {
    const TabItem = [
      {
        title: 'Stations',
        component: () => <Stations />,
      },
      {
        title: 'Aircraft Type',
        component: () => <AircraftTypeLanding />,
      },
      {
        title: 'Aircraft Registration',
        component: () => <AircraftRegLanding />,
      },
      {
        title: 'Delay Codes',
        component: () => <DelayCodesLanding />,
      },
    ];

    return (
      <>
        <Tabs className="main-tab">
          <TabList className="tab-main-div">
            <div className="tab-head-div">Select</div>
            {TabItem.map((tab, index) => (
              <Tab className="tablist" key={index}>
                {tab.title}
              </Tab>
            ))}
          </TabList>

          {TabItem.map((tab, index) => (
            <TabPanel className="tab-panel" key={index}>
              <tab.component />
            </TabPanel>
          ))}
        </Tabs>
      </>
    );
  }
}

export default DataSelect;
