import React, { Component } from 'react';
import { connect } from 'react-redux';
import '../../../Utils/Aircraft.scss';
import { Dropdown } from '../../Atoms/DropdownComponent';
import Input from '../../Atoms/InputElement';
import Button from '../../Atoms/Button';
import { AircraftRegActions } from '../../../Store/AircraftReg/action';
import { commonAction } from '../../../Store/commonActivity/action';

class AircraftReg extends Component {
  constructor(props) {
    super(props);
    this.state = {
      eclasscapacity: '',
    };
  }

  obj = {
    id: this.props.acregValue.id,
    aircraftregno: this.props.acregValue.aircraftregno,
    aircrafttype: this.props.acregValue.aircrafttype,
    cargopayload: this.props.acregValue.cargopayload,
    owner: this.props.acregValue.owner,
    overallpayload: this.props.acregValue.overallpayload,
    eclasscapacity: this.props.acregValue.eclasscapacity,
  };

  componentDidMount() {
    this.setState({
      eclasscapacity: this.props.acregValue.eclasscapacity,
    });
  }

  AcRegData = () => {
    const keys = Object.keys(this.obj);
    let flag = true;
    if (!this.props.isCreateOrupdate.isCreate) {
      for (var i = 0; i < keys.length; i++) {
        if (keys[i] === 'id') {
          keys.splice(i, 1);
        }
      }
    }

    let newKey = [
      {
        key: 'aircraftregno',
        value: 'AC Registration',
      },
      {
        key: 'aircrafttype',
        value: 'AC Type Category',
      },
    ];

    for (let index = 0; index < newKey.length; index++) {
      const element = newKey[index];
      if (!this.obj[element.key]) {
        flag = false;
        commonAction.openAlert({
          title: 'Warning',
          content: element.value + ' is empty',
        });
        return false;
      }
    }

    // Object.keys(this.obj).map(key => {
    //   let newKey = ['aircraftregno', 'aircrafttype'];
    //   if (
    //     newKey.includes(key) &&
    //     (this.obj[key] === '' || this.obj[key] === undefined)
    //   ) {
    //     flag = false;
    //     commonAction.openAlert({
    //       title: 'Warning',
    //       content: key + ' is empty',
    //     });
    //     return false;
    //   }
    // });
    if (flag) {
      var newObj = {};
      keys.map(key => {
        if (this.obj[key] !== '') {
          newObj[key] = this.obj[key];
        }
      });
      let isEdit = this.props.isCreateOrupdate.isCreate;

      this.props.AddAcRegsubmit(
        { ...newObj, eclasscapacity: this.state.eclasscapacity },
        isEdit
      );
      this.props.cancel();
    }
  };
  showWarning = () => {
    if (this.props.isCreateOrupdate.isCreate) {
      commonAction.openAlert({
        title: 'Warning',
        content: 'AC Registration is read only Field cannot be edited',
      });
    }
  };

  numberOnly = e => {
    const re = /^[0-9\b]+$/;
    if (e.target.value === '' || re.test(e.target.value)) {
      this.setState({ eclasscapacity: e.target.value });
    } else {
      commonAction.openAlert({
        title: 'Warning',
        content: 'Economy Class value should be number',
      });
    }
  };
  render() {
    return (
      <div>
        <div className="ac-border border-btmnone">
          <div className="title Aircraft-Details">Aircraft Details</div>
          <div className="ac-details">
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title">
                AC Registration<span className="mandatory-star"> *</span>
              </div>
              <div className="none" onClick={this.showWarning}>
                <Input
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.aircraftregno = e.target.value;
                  }}
                  defaultValue={this.props.acregValue.aircraftregno}
                  disabled={
                    this.props.acregValue.aircraftregno === null
                      ? true
                      : this.props.acregValue.aircraftregno.length > 0
                      ? true
                      : false
                  }
                />
              </div>
            </div>
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title">
                AC Type Category<span className="mandatory-star"> *</span>
              </div>
              <div>
                <Input
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.aircrafttype = e.target.value;
                  }}
                  defaultValue={
                    this.props.acregValue.aircrafttype === null
                      ? '-'
                      : this.props.acregValue.aircrafttype
                  }
                />
              </div>
            </div>
            <div className="ac-reg-div">
              <div className="ac-reg-title">Owner</div>
              <div>
                <Input
                  placeholder="Enter"
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.owner = e.target.value;
                  }}
                  defaultValue={
                    this.props.acregValue.owner === null
                      ? '-'
                      : this.props.acregValue.owner
                  }
                />
              </div>
            </div>
          </div>
          <div className="title Aircraft-Details"> Capacity Details </div>
          <div className="ac-details">
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title"> Cargo Payload </div>
              <div>
                <Input
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.cargopayload = e.target.value;
                  }}
                  defaultValue={
                    this.props.acregValue.cargopayload === null
                      ? '-'
                      : this.props.acregValue.cargopayload
                  }
                />
              </div>
            </div>
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title"> Overall Payload </div>
              <div>
                <Input
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.overallpayload = e.target.value;
                  }}
                  defaultValue={
                    this.props.acregValue.overallpayload === null
                      ? '-'
                      : this.props.acregValue.overallpayload
                  }
                />
              </div>
            </div>
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title"> ETS(UTC) </div>
              <div>
                <Input disabled className="opacity" />
              </div>
            </div>
            <div className="ac-reg-div">
              <div className="ac-reg-title"> ACARS Type </div>
              <div>
                <Dropdown dropdownDisable />
              </div>
            </div>
          </div>
          <div className="ac-details pad-7t">
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title"> First Class </div>
              <div>
                <Input disabled className="opacity" />
              </div>
            </div>
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title"> Business Class </div>
              <div>
                <Input disabled className="opacity" />
              </div>
            </div>
            <div className="ac-reg-div pad-12r">
              <div className="ac-reg-title"> Premium Class </div>
              <div>
                <Input disabled className="opacity" />
              </div>
            </div>
            <div className="ac-reg-div">
              <div className="ac-reg-title"> Economy Class </div>
              <div>
                <Input
                  placeholder="Enter"
                  // onChange={e => {
                  //   this.obj.eclasscapacity = e.target.value;
                  // }}
                  onChange={this.numberOnly}
                  value={this.state.eclasscapacity}
                />
              </div>
            </div>
          </div>
        </div>
        <div className="Rectangle-116 pad-18r">
          <Button className="cancel-btn" ButtonHandler={this.props.cancel}>
            Cancel
          </Button>
          <Button className="save-btn" ButtonHandler={this.AcRegData}>
            Save
          </Button>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = {
  AddAcRegsubmit: AircraftRegActions.postAircraftRegDetails,
};
const mapStateToProps = state => {
  return {};
};
export default connect(mapStateToProps, mapDispatchToProps)(AircraftReg);
