import React, { Component } from 'react';
import '../../Molecules/Stations/Station.scss';
import InputElement from '../../Atoms/InputElement/index.js';
import { connect } from 'react-redux';
import Toggler from '../../Atoms/Toggler';
import { StationActions } from '../../../Store/Station/action';
import { commonAction } from '../../../Store/commonActivity/action';
import '../AddStation/AddStation.scss';
import Button from '../../Atoms/Button';

class AddStation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mgt: '',
    };
  }
  obj = {
    id: this.props.StationValue.id,
    station: this.props.StationValue.station,
    name: this.props.StationValue.name,
    icaocode: this.props.StationValue.icaocode,
    region: this.props.StationValue.region,
    city: this.props.StationValue.city,
    country: this.props.StationValue.country,
    gmtoffset: this.props.StationValue.gmtoffset,
    gmtvariance: this.props.StationValue.gmtvariance,
    isbasestation: this.props.StationValue.isbasestation,
    mgt: this.props.StationValue.mgt,
  };

  componentDidMount() {
    this.setState({
      mgt: this.props.StationValue.mgt,
    });
  }

  stationUpdate = () => {
    const keys = Object.keys(this.obj);
    if (!this.props.isCreateOrupdate.isCreate) {
      for (var i = 0; i < keys.length; i++) {
        if (keys[i] === 'id') {
          keys.splice(i, 1);
        }
      }
    }
    let flag = true;

    // keys.map(key => {
    //   if (
    //     this.obj.station === '' ||
    //     this.obj.name === '' ||
    //     this.obj.icaocode === '' ||
    //     this.obj.city === '' ||
    //     this.obj.country === ''
    //   ) {
    //     flag = false;
    //     commonAction.openAlert({
    //       title: 'Warning',
    //       content: 'Fields are empty',
    //     });
    //   }
    // });

    let newKey = [
      {
        key: 'station',
        value: 'Station',
      },
      { key: 'name', value: 'Name' },
      { key: 'city', value: 'City' },
      {
        key: 'country',
        value: 'Country',
      },
      {
        key: 'icaocode',
        value: 'ICAO Code',
      },
    ];

    for (let index = 0; index < newKey.length; index++) {
      const element = newKey[index];
      if (!this.obj[element.key]) {
        flag = false;
        commonAction.openAlert({
          title: 'Warning',
          content: element.value + ' is empty',
        });
        return false;
      }
    }

    if (flag) {
      var newObj = {};
      keys.map(key => {
        if (this.obj[key] !== '') {
          newObj[key] = this.obj[key];
        }
      });
      let isEdit = this.props.isCreateOrupdate.isCreate;
      this.props.AddStationsubmit({ ...newObj, mgt: this.state.mgt }, isEdit);
      this.props.cancel();
    }
  };
  showWarning = () => {
    if (this.props.isCreateOrupdate.isCreate) {
      commonAction.openAlert({
        title: 'Warning',
        content: 'Station Code is read only Field cannot be edited',
      });
    }
  };

  numberOnly = e => {
    const re = /^[0-9\b]+$/;
    if (e.target.value === '' || re.test(e.target.value)) {
      // this.obj.mgt = e.target.value;
      this.setState({ mgt: e.target.value });
    } else {
      commonAction.openAlert({
        title: 'Warning',
        content: 'MGT value should be number',
      });
    }
  };

  render() {
    return (
      <div>
        <div className="ac-border border-btmnone">
          <div className="station-main-div">
            <div className="pad-20b">
              <div className="station-datalist">
                <div className="station-text" label="Station">
                  Station
                  <span className="mandatory-star"> *</span>
                </div>
                <div onClick={this.showWarning}>
                  <InputElement
                    placeholder="Enter"
                    onChange={e => {
                      this.obj.station = e.target.value;
                    }}
                    defaultValue={this.props.StationValue.station}
                    disabled={
                      this.props.StationValue.station === null
                        ? true
                        : this.props.StationValue.station.length > 0
                        ? true
                        : false
                    }
                  />
                </div>
              </div>

              <div className="station-name">
                <div className="station-text">
                  Name<span className="mandatory-star"> *</span>
                </div>
                <InputElement
                  placeholder="Name"
                  onChange={e => {
                    this.obj.name = e.target.value;
                  }}
                  defaultValue={this.props.StationValue.name}
                />
              </div>
            </div>

            <div className="pad-20b">
              <div className="station-datalist width-25">
                <div className="station-text">
                  City<span className="mandatory-star"> *</span>
                </div>
                <InputElement
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.city = e.target.value;
                  }}
                  defaultValue={this.props.StationValue.city}
                />
              </div>
              <div className="station-datalist pad-12l width-25">
                <div className="station-text">Region</div>
                <InputElement
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.region = e.target.value;
                  }}
                  defaultValue={this.props.StationValue.region}
                />
              </div>

              <div className="station-datalist pad-12l width-25">
                <div className="station-text">
                  Country<span className="mandatory-star"> *</span>
                </div>
                <InputElement
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.country = e.target.value;
                  }}
                  defaultValue={this.props.StationValue.country}
                />
              </div>

              <div className="station-datalist pad-12l  width-25">
                <div className="station-text">
                  Base Station<span className="mandatory-star"> *</span>
                </div>
                <Toggler
                  DataToggle="toggle-dm-style"
                  value={
                    this.props.StationValue.isbasestation === 'Y' ? true : false
                  }
                  onoffstatus={value => {
                    this.obj.isbasestation = value.istoggle ? 'Y' : 'N';
                  }}
                />
              </div>
            </div>

            <div className="pad-26b">
              <div className="station-datalist width-25">
                <div className="station-text">
                  ICAO Code<span className="mandatory-star"> *</span>
                </div>
                <InputElement
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.icaocode = e.target.value;
                  }}
                  defaultValue={this.props.StationValue.icaocode}
                />
              </div>

              <div className="station-datalist pad-12l width-25">
                <div className="station-text">GMT Offset</div>
                <InputElement
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.gmtoffset = e.target.value;
                  }}
                  defaultValue={this.props.StationValue.gmtoffset}
                />
              </div>

              <div className="station-datalist pad-12l width-25">
                <div className="station-text">GMT Variance</div>
                <InputElement
                  placeholder="Enter"
                  onChange={e => {
                    this.obj.gmtvariance = e.target.value;
                  }}
                  defaultValue={this.props.StationValue.gmtvariance}
                />
              </div>

              <div className="station-datalist pad-12l width-25">
                <div className="station-text">MGT</div>
                <InputElement
                  placeholder="Enter"
                  onChange={this.numberOnly}
                  value={this.state.mgt}

                  // defaultValue={this.props.StationValue.mgt}
                />
              </div>
            </div>
          </div>
        </div>

        <div className="Rectangle-116 pad-18r">
          <Button className="cancel-btn" ButtonHandler={this.props.cancel}>
            Cancel
          </Button>
          <Button className="save-btn" ButtonHandler={this.stationUpdate}>
            Save
          </Button>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = {
  AddStationsubmit: StationActions.postStationDetails,
};
const mapStateToProps = state => {
  return {};
};
export default connect(mapStateToProps, mapDispatchToProps)(AddStation);
